@use "../general/mixins" as m;
@use "../general/variables" as v;

.CallToAction{
    display: flex;
    margin: 2rem auto;
    background-color: v.$orange-light;
    padding: 1rem;
    max-width: 98%;
    border-radius: 0.5rem;

    @include m.desktop{
        max-width: 65%;
    }

    @include m.desktop-lg{
        max-width: 50%;
    }

}


.CallToAction__button{  
    margin: auto;
    @include m.BlueButton;
    @include m.tablet{
    display: flex;
    justify-content: center;
    align-items: center;
    width: 22rem;
    a{
        font-size: 3.2rem
    }
}
}
