@use "./variables" as v;

//Media Queries

@mixin phone{
    @media (min-width: v.$phone) {
        
        @content;
    }    
    
}

@mixin tablet{
    @media (min-width: v.$tablet) {
        .h1{
            font-size: 4.6rem;
        }
        @content;
    }    
    
}


@mixin desktop{
    @media (min-width: v.$desktop) {
        
        @content;
    }    
    
}

@mixin desktop-lg{
    @media (min-width: v.$desktop-lg) {
        
        @content;
    }    
    
}

//Buttons

@mixin RedButton{
    background-color: v.$red;
    width: 12.54rem;
    height: 7.186rem;
    border: none;
    border-radius: 5px;  
    
   
    &:hover{
        background-color: v.$red-dark;
    }

    a {
        text-decoration: none;
        font-size: 2.5rem; 
        color:v.$white;
        font-style: normal;
    }

}

@mixin OrangeButton{
    background-color: v.$orange;
    // color: v.$white ;
    width: 12.54rem;
    height: 7.186rem;
    border: none;
    border-radius: 5px;
    &:hover{
        background-color: v.$orange-light;
    }

    a{
        font-size:  2.4rem;
        color: v.$white;
        text-decoration: none;
    }

  

}

@mixin BlueButton{
    background-color: v.$blue-dark;
    color: v.$white;
    width: 100%;
    max-width: 70vw;
    height: 10vh;
   
    border: none;
    border-radius: 5px;
    &:hover{
        background-color: v.$blue-light;
    }

    a{
        font-size:  1em;
        color: v.$white;
        text-decoration: none;
        font-style: normal;
    }

}


//other
@mixin grid($column, $space) {
    display: grid;
    grid-template-columns: repeat($column, 1fr);
    gap: $space;
}

@mixin ListReset {
    margin: 0;
    padding: 0;
    list-style: none;
}

@mixin BoxShadow{
    box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.2), 0 3px 10px 0 rgba(0, 0, 0, 0.2);
    padding: 1rem;
}

// Center a div

@mixin CenterDiv{
    display: block;
    margin-left: auto;
    margin-right: auto;
}