@use "../general/variables" as v;
@use "../general/mixins" as m;

//header

.TopMenu{
    @include m.desktop{
    display: flex;
    flex-flow: column wrap;
    
    }
}

.TopMenu__Container{
    display: flex;
    padding: 0.5rem;
    background-color: v.$blue-dark;
    justify-content: space-between;
    margin-bottom: 0;

    @include m.desktop{
        display: flex;
        justify-content: space-between;
    }
}

.TopMenu__Logo{
    display: flex;
    flex-flow: column nowrap;
    align-items: center;
    flex-grow: 1;

    @include m.desktop{
        display: flex;
        flex-flow: row nowrap;
        justify-content: space-around;
    }

}

.TopMenu__Logo--Image{
    
    height: 9rem;
    width: 9rem;
    border-radius: 9.5rem;
    margin-top: 1rem;

    @include m.tablet{
    height: 12.6rem;
    width: 12.6rem;
    border-radius: 9.5rem;
}
}
.TopMenu__Title{
    color: v.$gray-light;
    margin: 0;
    line-height: 1;
    text-align: center;

    @include m.tablet{
        font-size: 3.6rem;
    }
}

.TopMenu__Title--sp{
    font-size: 1rem;
    color: v.$gray-light;

    @include m.tablet{
        font-size: 2.4rem;
        margin: 0;
    }
}


//menu

.Navbar{
    margin-top: 0;
    display: flex;
    flex-flow: column nowrap;
    align-items: flex-end;
    gap: 1.5rem; 
    
    
    @include m.desktop{
        display: flex;
        flex-flow: row wrap;
        align-content: center;
        
    }
}

.Navbar__Menu{
    color: v.$orange-light;
    font-style: normal;
    font-size: 2.4rem;
    padding: 1rem 2rem;
    &:hover{
        color: v.$gray-light;
        
    }

    @include m.tablet{
        font-size: 3.6rem;    
    }
}

//Blog menu layout

.TopMenu__Container--Blog{
    display: flex;
    padding: 0.5rem;
    background-color: v.$gray-dark;
    justify-content: space-between;
    margin-bottom: 0;

    @include m.desktop{
        display: flex;
        justify-content: space-between;
    }
}

.TopMenu__Title--sp--Blog{
   font-size: 3.6rem
}

//Hamburger Menu


nav ul {
  display: flex;
}

nav li {
  margin: 0 10px;
  text-align: right;
  @include m.ListReset;
}

nav a {
  color: #fff;
  text-decoration: none;  
}

.hamburger-menu {
  display: none;
  cursor: pointer;
  margin: 1.5rem;

}

/* Media query for responsive menu */
    @media (max-width: 768px) {
    .hamburger-menu {
        display: block;
    }
    .menu {
        display: none;
        
    }
}

.bar {
  height: 3px;
  width: 25px;
  background-color: #fff;
  margin: 5px 0;
  transition: 0.4s;
}

.menu.show {
  display: flex;
  flex-direction: column;
  background-color: rgb(51, 51, 51, 0.9);
  position: absolute;
  top: 60px;
  left: 0;
  right: 0;
  padding: 10px !important ;
  gap: 1rem; 

}



  


//social media

.SocialMedia {
    display: flex;
    flex-wrap: nowrap;
    justify-content: flex-end;
    align-items: flex-start;
    width: 100%;
    background-color:v.$blue-dark !important;
    height: auto;
    overflow: auto;
    flex-direction: row;
    padding: 1rem;
    padding-right: 1.5rem;
}


.fa-brands{
    // display: flex;
    // align-items:center ;
    // justify-content: space-around;
    color: white;
    font-size: 2.4rem;
    &:hover{
        color: v.$orange-light;
    }

    @include m.tablet{
        font-size: 3.6rem;
        padding: 1.5rem 1.5rem;
    }
}

.credits{
    display: flex;
    background-color: black;
    justify-content: center;
}

.credits p{
    color: whitesmoke;
    font-size: small;
    font-weight: 200;
}