@use "../general/variables" as v;
@use "../general/mixins" as m;

.hero__blog::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: url(/build/img/BlogHeroImage.webp );
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center center;
    filter: brightness(60%);
}

.hero__button{
    width: 14.54rem;

    @include m.tablet {
        width: 18.54rem
    }
}

main{
    @include m.tablet{
        @include m.grid(2,1rem);
    }
}

.Blog{
    @include m.tablet{
        @include m.CenterDiv;
        width: 60vw;

    }
}

.blog__intro{
    p{
    padding: 1rem;
    }
}

i{
    color:  v.$blue-light;
}

.Blog__Section{
    @include m.tablet{        
    @include m.grid(2, 0.5rem)
    }
}

.Blog__Section-Title{
    text-align: center;
}

.Blog__Sections{
    display: flex;
    flex-direction: column;
    justify-content: center;
    // align-content: center;
    margin: 2rem 2rem;
    background-color: v.$blue-dark;
    border-radius: 1rem;
    @include m.BoxShadow;
    
    h2{
        color: v.$gray-light;
        text-align: center;
    }
    
    a{
        display: block;
        text-align: center;
        font-style: normal;
        align-content: center;

    }
}


.Blog__Sections__Button{
    background-color: v.$orange;
    margin: 1.5rem auto;
    padding: 2rem 1rem;
    width: 25vw;
    border-radius: 1rem;

    @include m.phone {
        width: 40vw;
    }
    
    @include m.tablet {
        width: 25vw;
        
    }
}