@use "../general/variables" as v;
@use "../general/mixins" as m;


.SocialMedia__blog {
    display: flex;
    flex-wrap: nowrap;
    justify-content: flex-end;
    align-items: flex-start;
    width: 100%;
    background-color:v.$gray-dark;
    height: auto;
    overflow: auto;
    flex-direction: row;
    margin-top: 0;
    gap: 1rem;
}

.ArticlesCards{
    @include m.tablet{
        @include m.grid (2,1rem);
    }
}

.FooterBlogSection{
    @include m.desktop{
        margin: 2rem;
        @include m.grid (2,1rem);
        @include m.BoxShadow;
        border-radius: 1rem;
    }
}

.TopMenu__Title--sp{
    a{
        color: v.$gray-light;
        &:hover{
            color: v.$orange-light;
        }
    }
}