@use "general/variables" as v;
@use "general/mixins" as m;

*{
    scroll-behavior: smooth;
}
html{
    font-size: 62.5%;
}

body {
    background-color: #f2f2f2;
    box-sizing: border-box;
    font-size: 19px;
    line-height: 1.725;
    &.FixBody{
        overflow: hidden;
    }
}

*, *:before, *:after {
  box-sizing: inherit;
}

main{
    flex-grow: 1;
}

//Typography
h1,h2, h3{
    font-family: "Raleway, sans-serif";
    font-weight: 700;
    color: v.$gray-dark;
    margin: 0;

}

h1{
    font-size: 3.6rem;
    @include m.tablet{
        font-size: 4.6rem;
    }
}

h2, h3{
    font-size: 2.4rem;

    @include m.tablet{
        font-size: 3.4rem;
    }
}

h3{
    font-style: italic;
    margin-top: 2rem;;
}

p{
    font-family: 'Merriweather', serif;
    line-height: 1.5;
    font-size: 1.6rem;
    color: v.$blue-dark;
    margin: 0.8rem;
    
    @include m.tablet{
        font-size: 2.6rem;
    }
}

a{
    text-decoration: none;
    color: v.$blue-light;
    font-style: italic;
    font-size: 1.6rem;
    
    @include m.tablet{
        font-size: 2.6rem;
    }

    &:hover{
        color: v.$blue-dark;
    }
}

img{
    width: 100%;
}

.Containter{    
    width: 95%;
    max-width: 120rem;
    margin: 0 auto;
}



.Columns{
    @include m.desktop{
        display: flex;
        gap: 2rem;
    }
}

.inConstruction{
    background-color: v.$red-dark;
    color: v.$gray-light;
    font-weight: 700;
    padding: 1rem ;
}

.Divider{
    border-bottom: 2px solid v.$gray-light;
}