@use "../general/variables" as v;
@use "../general/mixins" as m;

.CardArticle__Layout{
    display: flex;
    flex-flow: column nowrap;
    margin: 1rem ;
    border-radius: 1rem;
    @include m.BoxShadow;
    
  
}

.CardHeader{
    display: flex;
    justify-content: center;
    // padding: 1rem 6rem;
    background-color: v.$blue-light ;
    margin: 0 2rem;
    border-radius: 1rem;

    h2{
        color: v.$gray-light;
        padding-left: 1rem;
    }
    
}

.CardArticle__Image{
    width: 30vw ;
    height: auto;
    padding: 1rem 2rem 1rem 0;

    @include m.tablet{
        width: 12vw;
    }
    
}

.CardGeneral__text{
    margin: 0 2rem;
    p{
        @include m.desktop {          
        
        font-size: 1.8rem;
    }
}
}

.CardArticle__body{
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    margin-right: 2rem;
}

.CardArticle__button{
    @include m.OrangeButton;
    display: flex;
    align-items: center;
    justify-content: center;
    padding:1rem 0;
    height: 5.186rem;


}

