@use "../general/variables" as v;
@use "../general/mixins" as m;

.hero {
    position: relative !important;
    height: 100vh !important;
    display: flex !important;
    justify-content: center !important;
    align-items: center !important;
}
.hero::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    // background: url(/build/img/hero-image.webp);
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center center;
    filter: brightness(60%);
}

.hero__content{
    position:relative;
    text-align: center;
    margin: 0.625rem;
}

.hero__title{
    color: v.$gray-light ;
    margin-bottom: 0;
}

.hero__subtitle{
    font-size: 2rem;
    color: v.$orange;
}

.hero__text{
    margin: 0 1rem ;
    position: relative;
    color: v.$gray-light;
    text-align: center;
}


.hero__button{
    margin-top: 2rem;    
    @include m.RedButton;  

    @include m.tablet {

        a{
            font-size: 3rem;
        }
        
        .hero__button {
            width: 15.54rem;
            height: 8.186rem;
        }
    }
}
