@use "../general/variables" as v;
@use "../general/mixins" as m;

//Botom Menu

.BotomMenu__Container{
    padding: 0.5rem;
    display: flex;
    justify-content: flex-end;
    margin-bottom: 0;

    @include m.desktop{
        display: flex;
        justify-content: space-between;
    }
}

.BotomMenu{
    background-color: v.$blue-dark;
}




.BotomMenu__Navbar{
    display: flex;
    margin-top: 0;
    max-width: auto;
    gap: 1rem; 
    padding: 1rem;
}

.BotomMenu__Navbar--Menu{
    
    flex-wrap: nowrap;
    color: v.$gray-light;
    font-style: italic;
    font-size: 1.6rem;
    padding: 1rem 2rem;
    &:hover{
        color: v.$orange-light ;
    }

    @include m.tablet{
        font-size: 2.4rem;        
    }
}

// blog menu layout

.BotomMenu__Blog{
    display: flex;
    background-color: v.$gray-dark;

}

.BotomMenu__Navbar--Blog {
    margin-top: 0;
    max-width: auto;
    a{
    color: #FFA64D;
    font-style: normal;
    font-size: 1.6rem;
    padding: 0 1rem;
    }
}


