//social media

.SocialMedia {
    display: flex;
    flex-wrap: nowrap;
    justify-content: flex-end;
    align-items: flex-start;
    background-color:v.$gray-dark;
    height: auto;
    overflow: auto;
    flex-direction: row;
    margin-top: 0;
    gap: 1rem;
}

.fa-brands{
    display: flex;
    align-items:center ;
    justify-content: space-around;
    color: white;
    font-size: 1.5rem;
    &:hover{
        color: v.$orange-light;
    }

    @include m.tablet{
        font-size: 3.6rem;
        padding: 1.5rem 1.5rem;
    }
}

.credits{
    display: flex;
    background-color: black;
    justify-content: center;
}

.credits p{
    color: whitesmoke;
    font-size: small;
    font-weight: 200;
}