@use "../general/variables" as v;
@use "../general/mixins" as m;

.excerpt{
    
    @include m.desktop{
        display: flex;
        justify-content: space-evenly;
    }
    
}

.excerpt__header{
    display: flex;
    justify-content: space-around;
    align-items: center;
    background-color: v.$blue-light;
    border-radius: 0.5rem;
    width: 100%;
    height: 10rem;
    margin-right: 1rem;

    h2{
        color: v.$gray-light;
    }
}

.excerpt__image--style{
    width: 6.2rem;
    height: 6.5rem;
    border-radius: 0.5rem;

    @include m.tablet{
    width: 8.2rem;
    height: 8.5rem;
    }

}