html {
  font-size: 62.5%;
}

body {
  background-color: #f2f2f2;
  box-sizing: border-box;
  font-size: 19px;
  line-height: 1.725;
}
body.FixBody {
  overflow: hidden;
}

*, *:before, *:after {
  box-sizing: inherit;
}

main {
  flex-grow: 1;
}

h1, h2, h3 {
  font-family: "Raleway, sans-serif";
  font-weight: 700;
  color: #333333;
  margin: 0;
}

h1 {
  font-size: 3.6rem;
}
@media (min-width: 768px) {
  h1 {
    font-size: 4.6rem;
  }
  h1 .h1 {
    font-size: 4.6rem;
  }
}

h2, h3 {
  font-size: 2.4rem;
}
@media (min-width: 768px) {
  h2, h3 {
    font-size: 3.4rem;
  }
  h2 .h1, h3 .h1 {
    font-size: 4.6rem;
  }
}

h3 {
  font-style: italic;
  margin-top: 2rem;
}

p {
  font-family: "Merriweather", serif;
  line-height: 1.5;
  font-size: 1.6rem;
  color: #0C225A;
  margin: 0.8rem;
}
@media (min-width: 768px) {
  p {
    font-size: 2.6rem;
  }
  p .h1 {
    font-size: 4.6rem;
  }
}

a {
  text-decoration: none;
  color: #1C4DCA;
  font-style: italic;
  font-size: 1.6rem;
}
@media (min-width: 768px) {
  a {
    font-size: 2.6rem;
  }
  a .h1 {
    font-size: 4.6rem;
  }
}
a:hover {
  color: #0C225A;
}

img {
  width: 100%;
}

