  border-bottom: 2px solid #E6E6E6;
}

.hero {
  position: relative !important;
  height: 100vh !important;
  display: flex !important;
  justify-content: center !important;
  align-items: center !important;
}

.hero::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center center;
  filter: brightness(60%);
}

.hero__content {
  position: relative;
  text-align: center;
  margin: 0.625rem;
}

.hero__title {
  color: #E6E6E6;
  margin-bottom: 0;
}

.hero__subtitle {
  font-size: 2rem;
  color: #FF8203;
}

.hero__text {
  margin: 0 1rem;
  position: relative;
  color: #E6E6E6;
  text-align: center;
}

.hero__button {
  margin-top: 2rem;
  background-color: #ff0000;
  width: 12.54rem;
  height: 7.186rem;
  border: none;
  border-radius: 5px;
}
.hero__button:hover {
  background-color: #cc0000;
}
.hero__button a {
  text-decoration: none;
  font-size: 2.5rem;
  color: #ffffff;
  font-style: normal;
}
@media (min-width: 768px) {
  .hero__button .h1 {
    font-size: 4.6rem;
  }
  .hero__button a {
    font-size: 3rem;
  }
  .hero__button .hero__button {
    width: 15.54rem;
    height: 8.186rem;
  }
}

