@use "../general/variables" as v;
@use "../general/mixins" as m;

.ul-article{
       font-family: 'Merriweather', serif;
    line-height: 1.5;
    font-size: 1.6rem;
    color: v.$blue-dark;
    margin: 0.8rem;
    
    @include m.tablet{
        font-size: 2.6rem;
    }
}

.Containter-ArticleLayout{
    width: 95%;
    max-width: 120rem;
    margin: 0 auto;

    @include m.tablet {
        width: 80%;
        max-width: 120rem;
        margin: 0 auto;
    }

    @include m.desktop {
        width: 70%;
        max-width: 120rem;
        margin: 0 auto;
    }
    
}

.ArticleHeader__Title{
    margin-top: 1rem;
    text-align: center;
}

.ArticleContent{
    display: flex;
    flex-flow: column;
}



.ArticleContent__index--container{
    width: 80%;
    margin: auto;
    @include m.desktop{
        width: 60%
    }
}

.ArticleContent__index{

    ul{
        
        width: 100%;
    }
    
    li{
        @include m.ListReset;
        line-height: 3.2rem;
        padding-top: 1rem;
        width: 100%;
        
    }

    h3{
        margin-top: 4rem;
    }

}

.ArticleContent__index--link{       
    
    border-bottom: 3px solid v.$orange;
    font-size: 1.6rem;
    @include m.tablet{
        font-size: 2.4rem;
    }

    a{
        display: block;
        text-align: left;
        margin-left:0;
        width: 100%;
    }
    
}

.ArticleContent__Image {
   width: 100%;
   max-width: 30rem;
   height: 100%;
  object-fit: cover;
   margin: 2rem auto;
   @include m.tablet {
    width: 100%;
    max-width: 120rem;
    object-fit: cover;
   }

}

.ArticleContent__Image--Shadow{
    @include m.BoxShadow;
    border-radius: 1rem;
}

.RelatedArticlesTitle{
    text-align: center;
    margin: 2rem auto;

    h3{
    font-size: 3rem;
    }
   
}


.MoreArticles{
    @include m.tablet {
        @include m.grid(2,1rem);   
    }

    @include m.desktop {
        @include m.grid(3,1rem)
    }
}

.MoreButton{
    display: flex;
    flex-flow: row nowrap;
    justify-content: center;
    align-items: center;
    margin: 2rem auto;
    @include m.OrangeButton;
    width: 17rem;
    height: 4rem;
    margin-bottom: 2rem; 
    
}

.SignUp{
    margin-top: 6rem;
    p{
        font-size: 1.5rem;
        text-align: center;
        margin: 2rem auto;
    }
    
}

.infographic{
    height: 568px;
    width: 268px;
    margin: 0 auto;
    text-align: center;

}