@use "../general/variables" as v;
@use "../general/mixins" as m;


.hero__about {
    position: relative;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
}
.hero__about::before {
    content: "";
    position: absolute;
    width: 100%;
    height: 100%;
    background: url(/build/img/AboutHeroImage.webp);
    background-position: center center;
    background-size: cover;
    filter: brightness(60%);
}

.hero__title{
    font-size: 5rem;

    @include m.tablet{
        font-size: 7rem;
    }
}

.hero__subtitle{
    color: v.$gray-light;
    margin-bottom: 1rem;
    margin-top: 1rem;
    font-size: 1.6rem
}

.HeroAbout__button{
    
    @include m.BlueButton;

    @include m.tablet{
        height: 8vh;
        width: 100%;
        max-width: 38vw;
    a{
        font-size: 2.6rem
    }
}

    @include m.desktop{
        max-width: 24vw;
}

    @include m.desktop-lg{
        max-width: 16vw;
}
}

.HeroAbout__button-CTA{
    @include m.OrangeButton;
}

