@use "../general/variables" as v;
@use "../general/mixins" as m;

// hero header
.hero__portfolio {
    position: relative;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
}
.hero__portfolio::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: url(/build/img/ShakingHands.webp);
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center center;
    filter: brightness(60%);
}

.hero__text--portfolio{
    color: v.$blue-dark
}

// Portfolio Galery

.Galery{
    padding: 5rem 0;
    h2 {
        text-align: center;
    }
    p{
        margin: 1rem auto
    }
}

.PortfolioGalery {
    @include m.ListReset;
    @include m.grid(2, 2rem);
    @include m.phone {
        @include m.grid(2, 2rem);
    }
    @include m.tablet {
        @include m.grid(3, 2rem);
    }
    @include m.desktop {
        @include m.grid(6, 2rem);
    }
}



.Overlay{
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    width: 100%;
    height: 100%;
    display: grid;
    place-items: center;
    background-color: rgb(0 0 0 /0.5);
}

picture{
    display: block;
    max-width: 90rem;
}

.CloseButton{
    width: 5rem;
    height: 5rem;
    border-radius: 50%;
    background-color: v.$orange-light;
    color: v.$blue-dark;
    display: grid;
    place-items: center;
    font-weight: 900;
    &:hover{
        cursor: pointer;
    }
}

//Projects description

.Projects{
    @include m.ListReset;

    li{
        margin-bottom: 0.5rem;
    }    
}

.Galery__Subtitle{
    @include m.tablet{
    margin-top: 5rem;
    }

}