@use "../general/mixins" as m;

.ContentHome{
    display: flex;
    flex-direction: column;
    min-height: 100vh;
}

.MainHome{
    flex-grow: 1;
}
.hero__home {
    position: relative;
    width: 100vw;
    height: 63vh;
    display: flex;
    justify-content: center;
    align-items: center;

    @include m.tablet{
        height: 69vh;
    }

    @include m.desktop{
        height: 74vh;
        width: 99vw;

    }

    @include m.desktop-lg{
        height: 78vh;
    }
}
.hero__home::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: url(/build/img/hero-image.webp);
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center center;
    filter: brightness(60%);
}
.BotomMenu__HomePage{
    display: flex;
    justify-content: space-between;
    align-items: center;

}
.BotomMenu__image{
    padding: 0.5rem 0 0 0.5rem;
    
}
.BotomMenu__image--size{
    width: 8.4rem;
    height: 8.4rem;
    border-radius: 9.5rem;

}