@use "../general/variables" as v;

.FlexContainer {
    display: flex;
    flex-wrap: nowrap;
    justify-content: flex-end;
    align-items: flex-start;
    align-content: flex-start;
    width: 100%;
    background-color:v.$blue-dark ;
    height: auto;
    overflow: auto;
    flex-direction: row;
}

.FlexContainer div {
    width: auto;
    height: 2.5rem;
}

.fa-brands{
    display: flex;
    align-items:center ;
    justify-content: space-around;
    color: v.$gray-light;
    font-size: 1rem;
    margin: 0.5rem;
    

}

.credits{
    display: flex;
    background-color: v.$gray-dark;
    justify-content: center;
}

.credits p{
    color: whitesmoke;
    font-size: small;
    font-weight: 200;
}